.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


/* ======= CUSTOM STYLES ======== */

/*input:not([type=submit]):not([type=file]), textarea {
    font-size: 25px;
    padding: 3px 5px;
	width:150px;
}*/

input:invalid {
  background-color: #ffdddd;
}


.navbar{
	width:100%;
	font-family:"Helvetica Neue",Helvetica,Arial,sans-serif;
	height:35px;
	background-color:#080808;
	position:fixed;
	color:#428bca;
	font-size:24px;
	line-height:35px;
	cursor:default;
	-webkit-box-shadow:0 0 5px rgba(0,0,0,.8);
	        box-shadow:0 0 5px rgba(0,0,0,.8);
}

.referenceLink {
	padding:0px 5px;
	font-family:"Helvetica Neue",Helvetica,Arial,sans-serif;
	height:35px;
	bottom:5px;
	right:55px;
	position:absolute;
	background-color:#e02e0b;
	position:fixed;
	color:white;
	font-size:24px;
	line-height:35px;
	cursor:pointer;
	-webkit-box-shadow:0 0 5px rgba(0,0,0,.8);
	        box-shadow:0 0 5px rgba(0,0,0,.8);
}

.scalelabel{
    position: fixed;
    left: 35%;
    -webkit-transform: translate(-50%, 0);
        -ms-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
	/*bottom:11%;*/
}


.InputStyle {
	font-size:20px;
	padding:3px 5px;
	width:155px;
}

.UnitsStyle {
	display:inline-block;
	padding:6px 4px 6px 4px;
	background-color:#ffefdb;
	width:35px;
	color:#000000;
	border-radius:8px;
}

.InputBox {
	margin-bottom:15px;
}

.RadioBox {
	margin-bottom:15px;
	text-align:center;
}

.ResetButton {
	background-color: #4CAF50; /* Green */
	border: none;
	color: white;
	padding: 10px 32px;
	text-align: center;
	text-decoration: none;
	display: inline-block;
	font-size: 16px;
	border-radius: 5px;
}


.hide {
	display:none;
}

#outer-container {
	height:100vh;
	overflow:hidden;
}

#canvasWrapper {
	  overflow:hidden;
	  height:80vh;
	  position:relative;
	  top:35px;
}

@media (max-width: 426px)
{
	.ResetButton {
		background-color: #4CAF50; /* Green */
		border: none;
		color: white;
		padding: 7px 15px;
		text-align: center;
		text-decoration: none;
		display: inline-block;
		font-size: 14px;
		border-radius: 5px;
	}

	.InputBox {
		margin-bottom:10px;
		font-size:17px;
		text-align:left;
	}

	.RadioBox {
		margin-bottom:15px;
		font-size:14px;
		text-align:center;
	}

	.navbar {
		width:100%;
		font-family:"Helvetica Neue",Helvetica,Arial,sans-serif;
		height:25px;
		background-color:#080808;
		position:fixed;
		color:#428bca;
		font-size:17px;
		line-height:25px;
		cursor:default;
		-webkit-box-shadow:0 0 5px rgba(0,0,0,.8);
		        box-shadow:0 0 5px rgba(0,0,0,.8);
	}
	
	.referenceLink{
		padding:0px 5px;
		font-family:"Helvetica Neue",Helvetica,Arial,sans-serif;
		height:25px;
		bottom:0px;
		right:10px;
		position:absolute;
		background-color:#e02e0b;
		position:fixed;
		color:white;
		font-size:17px;
		line-height:25px;
		cursor:pointer;
		-webkit-box-shadow:0 0 5px rgba(0,0,0,.8);
		        box-shadow:0 0 5px rgba(0,0,0,.8);
	}
	
	.InputStyle {
		font-size:17px;
		padding:1px 3px;
		width:120px;
	}
	
	.UnitsStyle {
		display:inline-block;
		padding:2px 5px 4px 5px;
		background-color:#ffefdb;
		width:25px;
		font-size:17px;
		color:#000000;
		border-radius:4px;
	}	
	
	.bm-menu {
	  padding: 0em 1em 0!important;
	}	
}


/* ======= SIDEBAR ====== */

/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 20px;
  top: 55px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  /*background: #373a47;*/
  background: #505359;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

.bm-menu-wrap {
	/*overflow-y:hidden;*/
}

/* General sidebar styles */
.bm-menu {
  background: #505359;
  padding: 0.5em 1em 0;
  font-size: 1.15em;
  /*overflow:initial!important;*/
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #505359;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0 0.75em 0.5em 0.8em;
  height:initial!important;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}


